<template>
	<view class="body">
		<view class="cell" v-for="(item,index) in data.data" :key="index">
			<view class="head">
				<view class="headLeft">{{item.goods_sub_title}}</view>
				<view class="headRight">{{item.money}}</view>
			</view>
			<view class="text"><u-count-down :time="item.group_adjust.end_time * 1000" format="HH:mm:ss"></u-count-down>后结束</view>
			<view class="numberList">
				<view class="mine" v-for="(a,b) in item.group_adjust.student" :key="b">
					<image :src="a.avatar||'../../static/mine/user_head.png'"></image>
					<view class="icon" v-if="a.is_group_captain==1">团长</view>
				</view>
				<view class="other" v-for="x in 3-item.group_adjust.student.length" :key="x"></view>
			</view>
			<view class="buttons">
				<button class="new" v-show="item.group_adjust.group_state==0" open-type="share" @click="share(item)">点击邀请好友</button>
				<view class="success" v-show="item.group_adjust.group_state==1">拼团成功</view>
				<view class="defults" v-show="item.group_adjust.group_state==2">拼团失败</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		myGroup, 
	} from '@/common/http.api.js';
	export default {
		data() {
			return{
				token:'',
				data:{},
				group_id:'',
				name:'',
				price:'',
				time:'',
				pages:1,
				allPages:''
			}
		},
		onShareAppMessage() {
			//分享
			wx.showShareMenu({
		      withShareTicket: true,
		      menus: ['shareAppMessage', 'shareTimeline']
		     })
		    return {
			  title: '考研调剂平台小程序',
		      path: '/pages/review/review?group_id='+this.group_id+'&name='+this.name+'&price='+this.price+'&time='+this.time,
			  imageUrl:'/static/review/help_share.jpg', //设置分享图片
		      // promise 
		    }
		},
		onShareTimeline: function () {
			//分享监听事件
			return {
			  title: '	考研调剂平台小程序',
			}
		},
		// 滑动到页面底部加载下一页
		onReachBottom(){
			// 判断是否有数据
			if(this.pages>=this.allPages){
				this.$u.toast("暂无更多")
			}else{
				this.pages++
				console.log(this.pages)
				myGroup({token:this.token, page:this.pages}).then(res=>{
					console.log(res)
					this.data.data.push(res.data.data.data)
				}).catch(err=>{
					console.log(err)
				})
			}
			// this.getData(); // 获取数据
			console.log('我滚动到底部了')
		},
		onLoad() {
			this.token = store.state.vuex_token
			myGroup({token:this.token, page:this.pages}).then(res=>{
				console.log(res)
				this.data = res.data.data
				this.allPages = res.data.data.last_page
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			share(item) {
				this.group_id = item.group_id
				this.name = item.goods_sub_title
				this.price = item.goods_price
				this.time = item.group_adjust.end_time
			}
		}
	}
</script>

<style lang="scss">
	.body{
		width: 100vw;
		min-height: 96vh;
		background-color: #f7f6fb;
		padding: 2vh 0;
		.cell{
			background-color: #fff;
			margin: 0 5%;
			margin-bottom: 30rpx;
			padding: 30rpx;
			border-radius: 20rpx;
			.head{
				display: flex;
				justify-content: space-between;
				padding-bottom: 20rpx;
				border-bottom: 1rpx solid #eee;
				.headLeft{
					padding-left: 50rpx;
					background-image: url(../../static/mine/car_icon.png);
					background-repeat: no-repeat;
					background-position: left center;
					background-size: 30rpx;
					font-size: 32rpx;
				}
				.headLeft::after{
					content: '(拼团价)';
					color: #ff6666;
					font-size: 24rpx;
					font-weight: 500;
					display: inline-block;
				}
				.headRight{
					color: #e00000;
					font-size: 34rpx;
					font-weight: 500;
				}
				.headRight::before{
					content: '实付款 ￥';
					color: #000;
					font-size: 28rpx;
					display: inline-block;
				}
			}
			.text{
				font-size: 24rpx;
				margin-top: 20rpx;
				color: #a6a6a6;
				display: flex;
				align-items: center;
				justify-content: center;
				.u-count-down__text{
					color: #a6a6a6;
					font-size: 24rpx !important;
				}
			}
			.numberList{
				height: 150rpx;
				margin: 20rpx 120rpx;
				margin-top: 0;
				display: flex;
				justify-content: space-around;
				align-items: center;
				.mine{
					position: relative;
					image{
						width: 90rpx;
						height: 90rpx;
						border-radius: 50%;
						border: 1rpx solid #fed555;
					}
					.icon{
						position: absolute;
						left: -10rpx;
						top: 0rpx;
						font-size: 20rpx;
						padding: 2rpx 8rpx;
						background-color: #ffd656;
						color: #fff;
						border: 1rpx solid #fff;
						border-radius: 50rpx;
					}
				}
				.other{
					border: 1rpx dashed #ccc;
					width: 90rpx;
					height: 90rpx;
					border-radius: 50%;
					background-image: url(../../static/mine/question_icon.png);
					background-repeat: no-repeat;
					background-position: center;
					background-size:  30rpx;
				}
			}
			.buttons{
				view{
					border-radius: 50rpx;
					line-height: 78rpx;
					color: #fff;
					font-size: 30rpx;
					text-align: center;
				}
				button{
					border-radius: 50rpx;
					line-height: 78rpx;
					color: #fff;
					font-size: 30rpx;
					text-align: center;
				}
				.new{
					background-color: #ffd656;
				}
				.success{
					background-color: #ee0000;
				}
				.defults{
					background-color: #b5b5b5;
				}
			}
		}
		.cell:last-child{
			margin-bottom: 0;
		}
	}
</style>